/* Set defaults */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html,
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', 'Open Sans', 'Arial', sans-serif;
  max-width: 1920px;
  color: #fff;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  background-color: #121314;
}

.btn-reset {
  appearance: none;
  border-radius: 0;
  opacity: 1;
  border: none;
  background-color: transparent;
  font: inherit;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 10px;
  height: 5px;
  background-color: #222;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #c50c0c;
}

.ul-reset {
  list-style: none;
}

.input {
  font: inherit;
  appearance: none;
  opacity: 1;
  transition: 0.5s;
}

.input:focus {
  outline: none;
  box-shadow: 1px 1px 30px 1px rgba(60, 86, 166, 0.3);
}

img,
video {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

/* Atomic */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

.d-f {
  display: flex;
}

.ttu {
  text-transform: uppercase;
}

.ta-c {
  text-align: center;
}

.ov-h {
  overflow: hidden;
}

.ov-s {
  overflow: scroll;
}

.black {
  color: #000;
}

/* Global */

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  display: inline-block;
  background-color: #c50c0c;
  color: #fff;
  border: none;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  padding: 25px 40px;
  text-transform: uppercase;
  line-height: 1;
}

.main-heading {
  font-size: 48px;
  font-weight: 900;
  color: #fff;
  text-transform: uppercase;
}

.link-more {
  font-weight: 600;
  font-size: 16px;
  padding-right: 35px;
  display: inline-block;
  color: #c50c0c;
  background-image: url(../img/icons/arrow_red.png);
  background-size: 21px 10px;
  background-repeat: no-repeat;
  background-position: right center;
}

.link-more_inverse {
  background-image: url(../img/icons/arrow_black.png);
}

.price {
  font-size: 48px;
  font-weight: 800;
  color: #fff;
}

.price__unit {
  color: #999999;
  font-size: 18px;
  font-weight: 400;
}

/* General */

.main-header {
  padding-top: 20px;
  background-color: transparent;
  height: 100px;
  margin-bottom: -100px;
  position: relative;
  z-index: 2;
}

.main-header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__logolink,
.main-header__logolink img {
  display: block;
}

.main-header__widget {
  margin-left: 30px;
}

.main-navigation {
  margin: 0 10px;
}

.main-navigation__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  align-items: baseline;
}

.main-navigation__list li {
  padding-bottom: 10px;
}

.main-navigation__list li + li {
  margin-left: 30px;
}

.main-navigation__list a {
  color: #fff;
}

.main-navigation__list li.active {
  border-bottom: 1px solid #c50c0c;
}

.widget-contacts {
  display: block;
}

.widget-contacts__phone {
  font-weight: 500;
  line-height: 1;
  color: #fff;
  font-size: 24px;
  display: block;
  padding-top: 10px;
  padding-bottom: 5px;
  padding-right: 35px;
  background-image: url(../img/svg/phone.svg);
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: right center;
}

.widget-contacts__address {
  margin: 0;
  padding: 0;
  text-align: center;
  color: #999999;
  font-size: 12px;
}

.footer .widget-contacts__phone {
  padding-right: 0;
  padding-left: 35px;
  background-position: left center;
}

.widget-contact-mail {
  font-size: 12px;
}

.widget-contact-mail a {
  color: #999999;
  text-decoration: underline;
}

.widget-social-links {
  display: inline-block;
  vertical-align: middle;
  width: 36px;
  height: 36px;
  background-size: 15px;
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #18191b;
}

.widget-social-links + .widget-social-links {
  margin-left: 15px;
}

.widget-social-links.fb {
  background-image: url(../img/svg/facebook-app-symbol.svg);
}

.widget-social-links.vk {
  background-image: url(../img/svg/vk.svg);
}

.widget-social-links.youtube {
  background-image: url(../img/svg/youtube-symbol.svg);
}

.widget-social-links.insta {
  background-image: url(../img/svg/instagram.svg);
}

.footer {
  padding-top: 50px;
  padding-bottom: 25px;
  background-color: #121314;
}

.footer .main-header {
  margin-bottom: 0;
  padding-top: 5px;
  padding-bottom: 35px;
}

.footer .slogan {
  color: #999999;
  font-weight: 400;
  font-size: 11px;
}

.main-footer {
  border-top: 1px solid #1f2021;
  padding-top: 35px;
}

.main-footer__row {
  align-items: center;
}

.main-footer__widget {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  color: #999999;
}

.main-footer__widget_copyright {
  font-size: 14px;
}

.main-footer__widget_social {
  text-align: right;
}

/* index.html */

.main-content {
  position: relative;
  z-index: 1;
}

.banner {
  text-align: center;
  height: 100vh;
  background-image: url(../img/index__firstScreen_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  z-index: 1;
}

.banner__link {
  position: absolute;
  bottom: 12.4vh;
  left: 50%;
  transform: translateX(-50%);
}

.about {
  padding-top: 200px;
  padding-bottom: 200px;
  background-image: url(../img/index__secondScreen_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.about__flex {
  display: flex;
  justify-content: space-between;
}

.about__wrap {
  width: calc(41.66666666666667% - 20px);
}

.about__h {
  margin-bottom: 40px;
}

.about__text {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 55px;
}

.about__thumb {
  background-color: #c50c0c;
  margin-right: 20px;
  margin-top: 20px;
}

.about__thumb img {
  position: relative;
  top: -20px;
  right: -20px;
}

.sales {
  padding-top: 150px;
  padding-bottom: 150px;
  background-image: url(../img/index__redScreen_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.sales__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 70px;
}

.sales__btns {
  display: flex;
  justify-content: space-between;
  width: 140px;
}

.sales__btn {
  border: none;
  width: 60px;
  height: 60px;
  background-color: #000;
  position: relative;
  cursor: pointer;
}

.sales__btn:before {
  content: '';
  display: block;
  width: 18px;
  height: 18px;
  border-top: 5px solid #fff;
  border-right: 5px solid #fff;
  position: absolute;
  top: 50%;
  left: 50%;
}

.sales__btn_next:before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.sales__btn_prev:before {
  transform: translate(-50%, -50%) rotate(-135deg);
}

.sales__slider {
  display: flex;
  justify-content: space-between;
}

.slider {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.slider__slide {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(50% - 20px);
}

.stock__link {
  display: block;
}

.stock__thumb {
  width: 100%;
  margin-bottom: 50px;
}

.stock__h {
  font-size: 24px;
  font-weight: 600;
  color: #fff;
  margin-bottom: 40px;
}

.stock__text {
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  margin-bottom: 35px;
}

.stock__more {
  color: #000;
}

.cards {
  padding-top: 160px;
  padding-bottom: 160px;
  background-image: url(../img/index__finalScreen_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.cards__h {
  margin-bottom: 80px;
}

.cards__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  padding-top: 50px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 190px;
  margin-bottom: 35px;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.card:nth-child(1) {
  background-image: url(../img/index__cards_card1.jpg);
}

.card:nth-child(2) {
  background-image: url(../img/index__cards_card2.jpg);
}

.card:nth-child(3) {
  background-image: url(../img/index__cards_card3.jpg);
}

.card_profitable .card__name {
  color: #c50c0c;
}

.card__name {
  font-size: 27px;
  font-weight: 800;
  text-align: center;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.card__time {
  font-size: 16px;
  font-weight: 400;
  color: #999999;
  text-align: center;
  margin-bottom: 50px;
}

.card__price {
  padding-bottom: 45px;
  text-align: center;
  margin: 0;
}

.card__features {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
}

.card__feature {
  font-size: 16px;
  font-weight: 400;
  padding-left: 25px;
  background-image: url(../img//icons/checkmark.png);
  background-repeat: no-repeat;
  background-size: 14px 14px;
  background-position: left center;
}

.card__feature + .card__feature {
  margin-top: 20px;
}

.card__buy {
  position: absolute;
  bottom: 0;
  left: 50%;
  padding-left: 65px;
  padding-right: 65px;
  transform: translate(-50%, 50%);
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .main-footer__widget {
    width: calc(50% - 20px);
  }

  .card {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .cards__list {
    justify-content: center;
  }

  .card {
    width: calc(83.33333333333334% - 20px);
  }
}